import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';

import { EventService } from '../../angular-services/event.service';
import { Event } from '../../classes/event';

@Component({
  selector: 'app-edit-event-form-admin',
  templateUrl: './edit-event-form.component.html',
  styleUrls: ['./edit-event-form.component.css']
})
export class EditEventFormComponent implements OnInit {

  @Output() eventName = new EventEmitter<string>();
  formEditEvent: FormGroup;
  event: Event = new Event();
  alert: any = { type: '', message: '', show: false };

  constructor(private eventService: EventService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.initForm();
    this.getEvent();
  }

  initForm() {
    this.formEditEvent = new FormGroup({
      name: new FormControl('', Validators.required),
      date: new FormControl('', Validators.required),
      description: new FormControl('', Validators.required),
      status: new FormControl('', Validators.required)
    });
  }

  getEvent() {
    let key = this.route.snapshot.paramMap.get('id');
    this.eventService.getEvent(key).subscribe(
      response => {
        if (response !== undefined) {
          this.event = response;
          this.eventName.emit(this.event.name);
          this.loadForm();
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao salvar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  loadForm() {
    this.formEditEvent.patchValue({
      name: this.event.name,
      date: this.formatDate(new Date(this.event.date)),
      description: this.event.description,
      status: this.event.status+''
    });
  }

  formatDate(date) {
    let day = '';
    let month = '';
    let year = '';

    day = date.getDate();
    month = date.getMonth()+1;
    year = date.getFullYear();

    if (parseInt(day) < 10) {
      day = `0${day}`;
    }
    if (parseInt(month) < 10) {
      month = `0${month}`;
    }

    let response = `${year}-${month}-${day}`;

    return response;
  }

  save() {
    if (this.formEditEvent.valid) {
      let key = this.route.snapshot.paramMap.get('id');
      let data = this.formEditEvent.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      let newDate = new Date(data.date);
      newDate.setDate(newDate.getDate()+1);
      data.date = new Date(newDate).getTime();
      this.eventService.updateEvent(key, data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }

}
